@import "../../Components/commonStyle.less";


.homePage {
    p {
        margin: 0;
        padding: 0;
    }

    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: rgba(19, 20, 44, 1);

    // 头部样式
    header {
        position: relative;
        width: 100vw;
        height: 100*0.44vw;
        background: url('../../Assets/header/bannerBg@2x.png') no-repeat;
        background-size: 100%;

        hgroup {
            position: absolute;
            width: 100%;
            top: 100*0.34vw;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            color: #fff;
            text-align: center;
            z-index: 3;

            .banner-p1 {
                font-size: 86px;
                margin-bottom: 0;
                font-family: PingFang-SC-Heavy, PingFang-SC;
                font-weight: 800;
                color: rgba(255, 255, 255, 1);
                line-height: 120px;
                letter-spacing: 2px;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
                animation: bannerP1 5s;
            }

            .banner-p2 {
                font-size: 36px;
                margin-bottom: 0;
                color: @fontColor;
                animation: bannerP1 5s;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
            }
        }
    }

    // 头部与内容间线条样式
    .banner-line {
        position: absolute;
        width: 100%;
        height: 100*0.31vw;
        top: 32.5vw;
        background-image: url('../../Assets/header/line@2x.png');
        background-repeat: no-repeat;
        background-size: cover;

        animation: bannerLine 5s linear infinite;
        z-index: 1;
    }

    // 主体样式
    main {
        padding: 100px 0 ;
        background-color: rgba(19, 20, 44, 1);

        // 解释文字
        .explanation-box {
            ul {
                position: relative;
                z-index: 1;
                display: flex;
                flex-direction: row;
                justify-content: space-around;

                li {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    width: 25.6vw;
                    background-image: url(../../Assets/home/explanation-border@2x.png);
                    background-size: 100% 100%;
                    background-repeat: no-repeat;
                    border-radius: 20px;
                    padding: 60px 55px;


                    .explanation-logo {
                        position: relative;
                        width: 8.6vw;
                        height: 8.6vw;
                        margin-bottom: 10px;
                        background-image: url(../../Assets/home/explanation-icon-border@2x.png);
                        background-size: 100% 100%;
                        background-repeat: no-repeat;

                        .explanation-logo-img {
                            position: absolute;
                            width: 60%;
                            height: 60%;
                            margin-left: 20%;
                            margin-top: 20%;
                        }

                        .menu {
                            background-image: url(../../Assets/home/menu@2x.png);
                            background-size: 100% 100%;
                            background-repeat: no-repeat;
                        }

                        .aims {
                            background-image: url(../../Assets/home/aims@2x.png);
                            background-size: 100% 100%;
                            background-repeat: no-repeat;
                        }

                        .wallet {
                            background-image: url(../../Assets/home/wallet@2x.png);
                            background-size: 100% 100%;
                            background-repeat: no-repeat;
                        }


                    }

                    .line {
                        margin-bottom: 30px;

                        img {
                            width: 100%;
                        }
                    }

                    p {
                        font-size: 18px;
                        font-family: PingFangSC-Light, PingFang SC;
                        font-weight: 300;
                        color: rgba(255, 255, 255, 1);
                        line-height: 40px;
                        text-shadow: 0px 2px 6px rgba(68, 183, 182, 0.39);
                    }
                }

                li:hover,
                li:active,
                li:focus {
                    position: relative;
                    background-image: url(../../Assets/home/explanation-bg-hover@2x.png);
                    background-size: 100% 100%;
                    background-repeat: no-repeat;

                    .menu {
                        background-image: url(../../Assets/home/menu-hover@2x.png);
                        background-size: 100% 100%;
                        background-repeat: no-repeat;
                    }

                    .aims {
                        background-image: url(../../Assets/home/aims-hover@2x.png);
                        background-size: 100% 100%;
                        background-repeat: no-repeat;
                    }

                    .wallet {
                        background-image: url(../../Assets/home/wallet-hover@2x.png);
                        background-size: 100% 100%;
                        background-repeat: no-repeat;
                    }
                }
            }
        }

        // 特点
        .feature-box {
            margin-top: 150px;

            &>section {
                display: flex;
                flex-direction: row;
                justify-content: start;
                height: 370px;

                .feature-icon-box {
                    position: relative;
                    width: 42.7vw;

                    img {
                        position: absolute;
                    }

                    .feature-line {
                        width: 20.5vw;
                        top: 20%;
                    }

                    .feature-base {
                        width: 19.4vw;
                        left: 38%;
                    }

                    .feature-adorn {
                        width: 8vw;
                        right: 30%;
                        top: -6%;
                        animation: adornLift 2s ease infinite alternate;
                    }

                    .lightning {
                        width: 5.5vw;
                        right: 32%;
                        top: -12%;
                    }

                    .smiley-line {
                        right: 0;
                        transform: rotateY(180deg)
                    }

                    .smiley-base {
                        left: 16%;
                    }

                    .smiley-adorn {
                        left: 30%;
                    }

                    .smiley {
                        width: 6.5vw;
                        left: 31%;
                        top: -14%;
                    }

                    .people {
                        width: 6vw;
                        right: 32%;
                        top: -12%;
                    }
                }

                .feature-detail {
                    width: 40vw;

                    .feature-detail-title {
                        // width: 17vw;
                        display: inline-block;
                        padding: 0 2vw;
                        font-size: 36px;
                        font-weight: 800;
                        color: rgba(100, 223, 222, 1);
                        line-height: 50px;
                        letter-spacing: 3px;
                        background-image: url(../../Assets/home/feature-detail-bg@2x.png);
                        background-size: 10vw;
                        background-repeat: no-repeat;
                        background-position: center;
                        text-align: center;
                        padding-bottom: 30px;
                    }

                    .feature-detail-txt {
                        font-size: 18px;
                        font-family: PingFangSC-Light, PingFang SC;
                        font-weight: 300;
                        color: rgba(255, 255, 255, 1);
                        line-height: 38px;
                        text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
                    }
                }
            }

            .feature-interesting {
                .feature-detail {
                    margin-left: 17.3vw;
                }
            }

            &>section:hover {
                .feature-detail-title {
                    color: rgba(255, 255, 255, 1);
                    letter-spacing: 3px;
                    text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.6);
                }

                .lightning {
                    animation: lightningLift 2s ease infinite alternate;
                }

                .smiley {
                    animation: smileyLift 2s ease infinite alternate;
                }

                .people {
                    animation: peopleLift 2s ease infinite alternate;
                }
            }
        }

        // 联系我们
        .contact-us-box {
            position: relative;

            margin-top: 30px;

            .contact-us-bg {
                position: relative;
                height: 44vw;

                img {
                    position: absolute;
                }

                img:nth-child(1) {
                    width: 22.2vw;
                    top: 66%;
                }

                img:nth-child(2) {
                    width: 79vw;
                    left: 18.3vw;
                }

                img:nth-child(3) {
                    width: 7vw;
                    right: 27%;
                    animation: contactLogo 4s linear infinite alternate;

                }

                img:nth-child(4) {
                    right: 0;
                    top: 10%;
                    width: 15vw;
                }

                img:nth-child(5) {
                    right: 0;
                    top: 33%;
                    width: 6vw;
                }

                img:nth-child(6) {
                    width: 10vw;
                    right: 25.5%;
                    top: 2%;
                    animation: contactAdorn 5s linear infinite;

                }

                img:nth-child(7) {
                    width: 10vw;
                    left: 22.5%;
                    top: 35%;
                    opacity: .6;
                    animation: contactAdorn2 5s linear infinite;
                }

                img:nth-child(8) {
                    width: 10vw;
                    left: 22.5%;
                    top: 14%;
                    opacity: .6;
                    animation: contactAdorn3 5s linear infinite;

                }
            }

            .contact-us-txt {
                position: absolute;
                left: 16.5%;
                top: 8%;

                p {
                    font-size: 36px;
                    margin-left: 20px;
                    font-family: PingFangSC-Regular, PingFang SC;
                    font-weight: 400;
                    color: rgba(255, 255, 255, 1);
                    line-height: 50px;
                    margin-bottom: 15px;
                    text-shadow: 0px 2px 14px rgba(0, 214, 255, 0.5);
                }

                .uploadBtn {
                    position: relative;
                    display: flex;
                    flex-direction: row;
                    justify-content: start;
                    align-items: center;
                    width: 23.6vw;
                    height: 7.05vw;
                    margin-bottom: 10px;
                    cursor: pointer;

                    .btn-bg {
                        position: absolute;
                        width: 100%;

                    }

                    span {
                        font-size: 36px;
                        font-family: PingFangSC-Regular, PingFang SC;
                        font-weight: 400;
                        color: rgba(255, 255, 255, 1);
                        line-height: 50px;
                        text-shadow: 0px 2px 14px rgba(0, 214, 255, 0.5);
                        padding-bottom: 5px;
                    }

                    .btn-icon {
                        width: 4.5vw;
                        margin-left: 30px;
                        margin-right: 20px;
                    }

                    .googlePlay {
                        margin-right: 7px;
                    }
                }

            }
        }
    }
    .record{
        padding-bottom: 10px;
    }

}


.enTxtStyle {
    header {
        hgroup {
            .banner-p1 {
                font-size: 68px;
                font-family: PingFang-SC-Heavy, PingFang-SC;
                font-weight: 800;
                color: rgba(255, 255, 255, 1);
                line-height: 95px;
                letter-spacing: 2px;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
                animation: bannerP1 5s;
            }

            .banner-p2 {
                font-size: 45px;
                font-family: PingFang-SC-Bold, PingFang-SC;
                font-weight: bold;
                color: rgba(100, 223, 222, 1);
                line-height: 63px;
                letter-spacing: 1px;
                text-shadow: 0px 2px 6px rgba(100, 223, 222, 0.6);
                animation: bannerP1 5s;
            }
        }
    }

    // 头部与内容间线条样式
    .banner-line {
        position: absolute;
        width: 100%;
        height: 100*0.31vw;
        top: 32.5vw;
        background-image: url('../../Assets/header/line@2x.png');
        background-repeat: no-repeat;
        background-size: cover;
        animation: bannerLine 5s linear infinite;
        z-index: 1;
    }

    // 主体样式
    main {

        // 解释文字
        .explanation-box {
            ul {
                li {
                    p {
                        font-size: 16px;
                        font-family: PingFang-SC-Medium, PingFang-SC;
                        font-weight: 500;
                        color: rgba(255, 255, 255, 1);
                        line-height: 28px;
                        text-shadow: 0px 2px 6px rgba(68, 183, 182, 0.39);
                    }
                }
            }

            // 特点
            .feature-box {
                &>section {
                    .feature-detail {
                        .feature-detail-title {
                            font-size: 30px;
                            font-family: PingFang-SC-Heavy, PingFang-SC;
                            font-weight: 800;
                            color: rgba(255, 255, 255, 1);
                            line-height: 42px;
                            letter-spacing: 2px;
                            text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.6);
                        }

                        .feature-detail-txt {
                            font-family: PingFang-SC-Medium, PingFang-SC;
                            font-weight: 500;
                            line-height: 30px;
                        }
                    }
                }
            }

        }

        // 联系我们
        .contact-us-box {
            .contact-us-txt {
                p {
                    font-size: 34px;
                    line-height: 48px;
                }
            }
        }
    }

}


@keyframes bannerP1 {
    0% {
        transform: scale(2);
        opacity: 0;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes bannerLine {
    0% {
        top: 40.5vw;
        opacity: 0;
    }

    50% {
        top: 35.5vw;
        opacity: 1;
    }

    100% {
        top: 30.5vw;
        opacity: 0;
    }
}

@keyframes adornLift {
    0% {
        top: -6%;
        opacity: 0;
    }

    100% {
        top: -18%;
        opacity: 1;
    }
}

@keyframes lightningLift {
    0% {
        top: -5%;
        opacity: 0;
    }

    100% {
        top: -13%;
        opacity: 1;
    }
}

@keyframes smileyLift {
    0% {
        top: -14%;
        opacity: 0;
    }

    100% {
        top: -20%;
        opacity: 1;
    }
}

@keyframes peopleLift {
    0% {
        top: -12%;
        opacity: 0;
    }

    100% {
        top: -20%;
        opacity: 1;
    }
}

@keyframes contactLogo {
    0% {
        top: 0%;
        opacity: 0;
    }

    50% {
        top: -3%;
        opacity: 1;
    }

    100% {
        top: -6%;
        opacity: 0;
    }
}

@keyframes contactAdorn {
    0% {
        top: 2%;
        opacity: 0;
    }

    50% {
        top: -2%;
        opacity: 1;
    }

    100% {
        top: -6%;
        opacity: 0;
    }
}

@keyframes contactAdorn2 {
    0% {
        top: 35%;
        opacity: 0;
    }

    50% {
        top: 31%;
        opacity: .6;
    }

    100% {
        top: 27%;
        opacity: 0;
    }
}

@keyframes contactAdorn3 {
    0% {
        top: 14%;
        opacity: 0;
    }

    50% {
        top: 10%;
        opacity: .6;
    }

    100% {
        top: 6%;
        opacity: 0;
    }
}