@import "../../Components/commonStyle.less";

nav {
    width: 100%;
    // height: 45px;
    background: linear-gradient(270deg, rgba(100, 223, 222, 0) 0%, rgba(100, 223, 222, 0.2) 47%, rgba(100, 223, 222, 0) 100%);
    border-bottom: 1px solid;
    border-image: linear-gradient(270deg, rgba(100, 223, 222, 0.8), rgba(100, 223, 222, 0), rgba(100, 223, 222, 1)) 1 1;


    .upload-box {
        display: inline-block;

        div {
            display: flex;
            justify-content: start;
            align-items: center;
            color: #fff;
            padding: 9.5px 0;
            cursor: pointer;

            .logoImg {
                width: 26px;
                height: 26px;
                margin-right: 15px;
            }

        }

        // span {
        //     cursor: pointer;
        // }

    }

    .logo-info {
        display: flex;
        justify-content: space-around;
        align-items: center;

        .loginBtn {
            color: @fontColor;
        }

        .switch-language-box {
            display: flex;
            justify-content: start;
            align-items: center;

            .headerMap {
                width: 18px;
                height: 18px;
                color: #fff;
            }

            .headerMap:last-of-type {
                fill: #fff;
            }

            .ant-select-arrow {
                color: #fff !important;
            }
        }

    }
}