footer {

    p,
    ul {
        margin: 0;
        padding: 0;
    }

    background:linear-gradient(270deg, rgba(100, 223, 222, 0.04) 0%, rgba(100, 223, 222, 0.1) 47%, rgba(100, 223, 222, 0.04) 100%);

    ul {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 45px 230px;

        li {
            display: flex;
            flex-direction: column;
            justify-content: start;

            p {

                font-size: 22px;
                font-family: PingFang-SC-Medium, PingFang-SC;
                font-weight: 500;
                color: rgba(100, 223, 222, 1);
                line-height: 30px;
            }

            a {

                font-size: 14px;
                font-family: PingFangSC-Light, PingFang SC;
                font-weight: 300;
                color: rgba(255, 255, 255, 1);
                line-height: 21px;
                // text-shadow: 0px 2px 4px rgba(100, 223, 222, 1);
                padding: 2px;
            }
        }

        li:nth-child(1) {
            flex-direction: row;

            img {
                width: 50px;
                height: 50px;
                margin-right: 10px;
            }

            p {

                font-size: 28px;
                font-family: PingFang-SC-Medium, PingFang-SC;
                color: rgba(100, 223, 222, 1);
            }
            span {
                font-size: 14px;
                font-family: PingFangSC-Semibold, PingFang SC;
                font-weight: 600;
                color: rgba(100, 223, 222, 1);
                line-height: 20px;
            }
        }

        li a:hover {

            font-size: 14px;
            font-family: PingFangSC-Light, PingFang SC;
            font-weight: 300;
            color: rgba(255, 255, 255, 1);
            line-height: 21px;
            text-shadow: 0px 2px 4px rgba(100, 223, 222, 1);
            padding: 2px;
    }
    }

    .explanationSegmentationImg {
        width: 100%;
        height: 12px;
    }

    .record {
        text-align: center;
        font-size: 14px;
        font-family: PingFangSC-Light, PingFang SC;
        font-weight: 300;
        color: rgba(255, 255, 255, 1);
        line-height: 20px;
        text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
        padding-bottom: 10px;
    }
}